:root {
  --bg-orange: #f4c63d;
  --bg-brown-light: #eadbc4;
  --bg-brown-dark: #453d3f;
  --bg-brown-extra-dark: #272727;
  --bg-brown-extra-light: #ded0ba;
  --bg-red: #d70206;
  --shadow: #0000004d;
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: var(--bg-brown-dark);
  background-color: var(--bg-brown-light);
  font-family: Oxygen, sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.7;
  overflow: hidden;
}

ul {
  list-style: none;
}

.hidden {
  visibility: hidden;
  transition: all 1s ease-in;
  display: none;
}

main {
  display: flex;
}

.btn {
  box-shadow: .5rem .5rem 1rem var(--shadow);
  border: none;
  border-radius: .5rem;
  padding: 1rem;
  transition: all .2s;
}

.btn:hover {
  transform: translateY(50%)scale(1.1);
}

.header {
  background-color: var(--bg-brown-dark);
  border-bottom: .3rem solid gray;
}

.header__list {
  margin-left: 2rem;
  list-style: none;
  display: flex;
}

.header__item {
  padding: .8rem;
}

.header__item:hover {
  background-color: var(--bg-brown-extra-dark);
}

.header__item a {
  color: var(--bg-brown-light);
  font-size: 1.4rem;
  text-decoration: none;
}

.sidebar {
  width: 35rem;
  height: 100vh;
  background-color: var(--bg-brown-extra-light);
  padding: 2rem 2rem 10rem 3rem;
  overflow-y: auto;
}

.sidebar__titles {
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 700;
  position: relative;
}

.sidebar__item {
  cursor: pointer;
  margin-left: 1.5rem;
  padding: .5rem;
  font-size: 1.2rem;
  position: relative;
}

.sidebar__list--1-1 {
  margin-left: 1.5rem;
}

.select:hover {
  color: var(--bg-brown-extra-light);
  background-color: var(--bg-brown-dark);
}

.menu {
  cursor: pointer;
}

.menu:after {
  content: "";
  width: 1rem;
  height: 1rem;
  background-image: url("up-arrow.dd8e7329.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  position: absolute;
  top: 50%;
  left: -1rem;
  transform: translate(-50%, -50%)rotateZ(90deg);
}

.app-container {
  width: 100%;
  position: relative;
}

.welcome {
  width: 25rem;
  height: auto;
  color: #453d3f4d;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -80%);
}

.welcome-icon {
  font-size: 12rem;
  animation: shake 2s ease-in-out infinite;
  display: block;
}

.request-containter {
  width: 100%;
  height: 100%;
  padding: 2rem;
}

.form-container {
  width: 100%;
  height: auto;
  background-color: var(--bg-orange);
  box-shadow: 1rem 1rem 1.5rem var(--shadow);
  border-radius: .5rem;
  padding: 1rem;
}

.method {
  color: var(--bg-brown-extra-dark);
  font-size: 2.4rem;
  position: relative;
}

.input__method {
  text-align: center;
  width: 6rem;
  height: 2.4rem;
  border: none;
  border-bottom: 2px solid var(--bg-brown-dark);
  background-color: #0000;
  font-size: 1.6rem;
}

.input__method:focus {
  outline: none;
}

.btn-submit {
  color: #fff;
  cursor: pointer;
  background-color: green;
  font-size: 1.8rem;
  font-weight: bold;
  position: absolute;
  right: 0;
  transform: translateY(50%);
}

.summary {
  border-collapse: collapse;
  text-align: center;
  box-shadow: .5rem .5rem 1rem var(--shadow);
  margin: 4.5rem auto 0;
}

.summary th {
  background-color: var(--bg-orange);
}

.summary tr:nth-child(2n) {
  background-color: var(--bg-brown-dark);
  color: #fff;
}

.summary td {
  padding: 0 3rem;
}

.response {
  text-align: center;
  color: var(--bg-red);
  margin-top: 1.5rem;
  font-size: 2rem;
}

.response span {
  color: var(--bg-brown-extra-dark);
  font-weight: bold;
}

.input {
  width: 10rem;
  color: inherit;
  border: none;
  border-bottom: 2px solid var(--bg-brown-dark);
  background-color: #0000;
  margin-right: 2.5rem;
  padding: .5rem;
  font-family: inherit;
  font-size: 1.6rem;
}

.input:focus {
  outline: none;
}

.label {
  font-size: 1.6rem;
}

.input#function {
  width: 30rem;
}

@keyframes shake {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

